import React, {ChangeEvent, DetailedHTMLProps, InputHTMLAttributes, KeyboardEvent, LabelHTMLAttributes} from 'react'
import classes from './SuperInputText.module.css'

// тип пропсов обычного инпута
type DefaultInputPropsType = DetailedHTMLProps<InputHTMLAttributes<HTMLInputElement>, HTMLInputElement>
type DefaultLabelPropsType = DetailedHTMLProps<LabelHTMLAttributes<HTMLLabelElement>, HTMLLabelElement>

// здесь мы говорим что у нашего инпута будут такие же пропсы как у обычного инпута
// (чтоб не писать value: string, onChange: ...; они уже все описаны в DefaultInputPropsType)
type SuperInputTextPropsType = DefaultInputPropsType  & { // и + ещё пропсы которых нет в стандартном инпуте
    onChangeText?: (value: string) => void
    onEnter?: () => void
    error?: string
    spanClassName?: string
}

type SuperLabelPropsType = DefaultLabelPropsType & {
    alt?: string
    someCustomProp?: string
}


const SuperInputText: React.FC<SuperInputTextPropsType & SuperLabelPropsType> = (
    {
        type, // достаём и игнорируем чтоб нельзя было задать другой тип инпута
        onChange, onChangeText,
        onKeyPress, onEnter,
        error,
        className, spanClassName,
        ...restProps// все остальные пропсы попадут в объект restProps
    }
) => {
    const onChangeCallback = (e: ChangeEvent<HTMLInputElement>) => {
        onChange // если есть пропс onChange
        && onChange(e) // то передать ему е (поскольку onChange не обязателен)

        onChangeText && onChangeText(e.currentTarget.value)
    }
    const onKeyPressCallback = (e: KeyboardEvent<HTMLInputElement>) => {
        onKeyPress && onKeyPress(e);

        onEnter // если есть пропс onEnter
        && e.key === 'Enter' // и если нажата кнопка Enter
        && onEnter() // то вызвать его
    }

    const finalSpanClassName = `${classes.error} ${spanClassName ? spanClassName : ''}`
    const finalInputClassName = `${classes.input} ${error ? classes.errorInput : ''}` // needed to be fixed with (?:)

    return (
        <div>
            <input
                placeholder={'Enter text here'}
                type={'text'}
                onChange={onChangeCallback}
                onKeyPress={onKeyPressCallback}
                className={`${className} + ${finalInputClassName} `} // не смешивается. Хз почему :(
                {...restProps} // отдаём инпуту остальные пропсы если они есть (value например там внутри)
            />
{/*            <label someCustomProp={`I'm not work`} alt={'!!!'} placeholder={'enter smth hier'}> </label>*/}
            {error && <div className={finalSpanClassName}>{error}</div>}
        </div>
    )
}

export default SuperInputText
